<template>
	<el-header style="height: 50px; padding-top: 5px;background: aliceblue">
		<el-form ref="form" label-width="100px">
			<div class="option_block">
				<el-form-item label="式子数量">
					<el-input-number v-model="total" step="1" min="1" style="width: 130px"></el-input-number>
				</el-form-item>
			</div>

			<div class="option_block" style="width: 165px;margin-left: 10px;">
				<el-checkbox-group v-model="optionSign" align="left">
					<el-checkbox label="0">加法</el-checkbox>
					<el-checkbox label="1">减法</el-checkbox>
					<el-checkbox label="2">乘法</el-checkbox>
					<el-checkbox label="3">除法</el-checkbox>
				</el-checkbox-group>
			</div>

			<div class="option_block" style="width: 120px;">
				<el-button type="primary" @click="generate">开始</el-button>
			</div>

			<div class="option_block" style="width: 160px;">
				<el-form-item label="成绩">
					<el-input placeholder="得分" v-model="score" readonly></el-input>
				</el-form-item>
			</div>

			<div class="option_block" style="width: 160px;">
				<el-form-item label="耗时">
					<el-input placeholder="耗时" v-model="takeTimes" readonly></el-input>
				</el-form-item>
			</div>
			<div>
				<el-button @click="finish" :disabled="!btndisabled">交卷</el-button>
			</div>
		</el-form>
	</el-header>
	<el-main>
		{{shizi}}
	</el-main>
</template>

<script>
	export default {
		name: 'compute',
		data: function() {
			return {
				total: 5,
				optionSign: ['0', '1'],
				shizi: [],
				score: 0,
				takeTimes: 0,
			}

		},
		methods: {
			handleChange(value) {
				console.log(value);
			},
			generate() {
				this.shizi=[];
				for (let i = 0; i < this.total; i++) {
					let obj = {};
					obj.num1 = Math.round(Math.random() * 100)
					obj.num2 = Math.round(Math.random() * 100)
					let optString = (this.optionSign[Math.floor((Math.random() * this.optionSign
						.length))]) //从this.optionSign 中随机生成  0 加 1减 2乘 3除
					let opt;
					switch (optString) {
						case '0':
							opt = '+';
							break;
						case '1':
							opt = '-';
							break;
						case '2':
							opt = '×';
							break;
						case '3':
							opt = '÷';
							break;
					}
					obj.opt = opt;
					this.shizi.push(obj)
				}
				console.log(this.shizi)
			}

		}
	}
</script>

<style>
	.line_div {
		width: 150px;
		float: left;
		margin-top: 5px;
		margin-bottom: 5px;
	}

	.numb {
		width: 30px;
		float: left;
	}

	.option {
		width: 10px;
		float: left;
	}

	.inputStyle {
		width: 20px;
	}

	.input_div {
		width: 22px;
		float: left;
	}

	.img_style {
		float: left;
	}

	.option_block {
		display: inline-block;
		float: left;
		height: 50px;
	}
</style>
